.intro-container {
  display: flex;
  margin-left: 4vw;
  margin-right: 4vw;
  align-items: center;
  justify-content: space-evenly;
}

.title {
  font-size: 8vw;
  font-weight: 600;
}

.subTitle {
  display: flex;
  flex-direction: column;
}

.arrowIcon {
  width: 0.8vw;
}

.readMore {
  display: flex;
  align-items: center;
}

.readMore span {
  font-size: 1.5vw;
}

.subtitleText {
  font-size: 1.1vw;
  color: rgb(196, 196, 196);
}
