.menu-container {
  margin-top: 2vw;
  display: flex;
  justify-content: center;
}

.green {
  width: 20vw;
  height: 26vw;
  border-radius: 1vw;
  background-color: #01af8b;
  margin-right: 1vw;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  cursor: pointer;
}

.womanImg {
  position: absolute;
  width: 15.1vw;
  transform: translateY(-1.4vw) translateX(1vw) scale(1.4);
  transition: transform 2s;
}

.green:hover > .womanImg {
  transform: translateY(5.5vw) translateX(6vw) scale(0.8);
}

.green:hover > .greenTitle {
  color: #1e1c1c;
}

.greenTitle {
  font-size: 2vw;
  font-weight: 900;
  margin-left: 2vw;
  margin-top: 0vw;
  color: #01af8b;
  transition: color 2s;
}

.green:hover > .PocketIcon {
  width: 3vw;
}

.PocketIcon {
  width: 0vw;
  margin-left: 2vw;
  margin-top: 6vw;
  margin-bottom: 4vw;
  transition: width 0.5s;
  color: #ffca43;
}

.green:hover > .greenSubtitle {
  color: #1e1c1c;
}

.greenSubtitle {
  margin-left: 2vw;
  font-weight: 900;
  font-size: 1vw;
  color: #01af8b;
  transition: color 2s;
}
.top {
  margin-top: 1vw;
}

.yellow {
  border-radius: 1vw;
  width: 19vw;
  height: 12.5vw;
  background-color: #ffca43;
  margin-bottom: 1vw;
  overflow: hidden;
  cursor: pointer;
}

.yellow:hover > .greenShirtImg {
  transform: translateX(14vw) translateY(-4vw) rotate(40deg);
}

.greenShirtImg {
  position: absolute;
  width: 6vw;
  transform: translateX(14vw) translateY(-2vw) rotate(40deg);
  transition: transform 2s;
}

.yellowBold {
  margin-top: 3vw;
  margin-left: 2vw;
  position: relative;
  color: #181818;
  font-size: 2.5vw;
  font-weight: 900;
}

.yellowLight {
  margin-left: 2vw;
  position: relative;
  color: #181818;
  font-size: 2.5vw;
}

.yellow:hover > .blueShirtImg {
  transform: rotate(-30deg) translateX(2vw) translateY(-1.5vw);
}

.blueShirtImg {
  width: 11vw;
  transform: rotate(-15deg) translateX(2vw);
  transition: transform 2s;
}

.aqua {
  border-radius: 1vw;
  width: 15vw;
  height: 12.5vw;
  background-color: #01af8b;
  margin-left: 1vw;
  cursor: pointer;
}

.aqua:hover > .grassesImg {
  transform: translateY(-3vw) translateX(5vw) scale(0.7);
}

.grassesImg {
  position: absolute;
  width: 15.1vw;
  transform: translateY(-5.5vw) translateX(0vw) scale(1);
  transition: transform 2s;
}

.aqua:hover > .aquaTitle {
  color: #1e1c1c;
}

.aquaTitle {
  margin-left: 1vw;
  margin-top: 2vw;
  font-size: 2.5vw;
  font-weight: 900;
  color: #01af8b;
  transition: color 2s;
}

.aqua:hover > .aquasSubtitle {
  color: #1e1c1c;
}

.aquasSubtitle {
  margin-left: 1vw;
  font-size: 2.5vw;
  color: #01af8b;
  transition: color 2s;
}

.yellow-aqua {
  display: flex;
}

.blue {
  border-radius: 1vw;
  width: 35vw;
  height: 12.5vw;
  background-color: #4ba0fe;
  cursor: pointer;
}

.blueItems {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 2vw;
  margin-right: 6vw;
}

.buttonBlue {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #181818;
  text-align: center;
  width: 6vw;
  height: 2vw;
  border-radius: 2vw;
  margin-bottom: 4vw;
}

.blueTitle {
  color: #181818;
  font-weight: 600;
  font-size: 1.1vw;
}

.blue:hover > .blueItems .shopContainer {
  transform: rotate(360deg);
  transition: all 2s;
}

.shopIcon {
  width: 12vw;
  transform: rotate(0deg);
  transition: all 2s;
}

.blue:hover > .tennisImg {
  transform: translateY(-5vw) translateX(18vw) rotate(-10deg) scale(1.5);
  transition: transform 2s;
}

.tennisImg {
  width: 15vw;
  position: absolute;
  transform: translateY(-8vw) translateX(20vw) rotate(-20deg);
  transition: transform 2s;
}

.glass {
  margin-left: 1vw;
  border-radius: 1vw;
  width: 15vw;
  height: 26vw;
  background-color: #4ba0fe;
  overflow: hidden;
  cursor: pointer;
}

.glass:hover > .glassesImg {
  transform: scale(1.2);
}

.glassesImg {
  width: 15vw;
  margin-top: 5vw;
  transition: transform 2s;
}

.glassTitle {
  position: absolute;
  transform: translateY(-25vw);
  margin-left: 2vw;
  font-size: 2vw;
  font-weight: 900;
  color: #18181800;
  transition: color 2s;
}

.glassSubtitle {
  position: absolute;
  transform: translateY(-22vw);
  margin-left: 2vw;
  font-size: 2vw;
  font-weight: 600;
  color: #18181800;
  transition: color 2s;
}

.glass:hover > .glassTitle {
  color: #181818;
}

.glass:hover > .glassSubtitle {
  color: #181818;
}

@media only screen and (max-width: 767px) {
  .menu-container {
    flex-direction: column;
    align-items: center;
  }

  .logo {
    font-size: 7vw;
  }

  .logo img {
    width: 7vw;
  }

  .navbar-items {
    visibility: hidden;
  }

  .navbar-search {
    visibility: hidden;
  }

  .subTitle {
    display: none;
  }

  .title {
    font-size: 10vw;
  }

  .green {
    width: 87vw;
    height: 35vw;
  }

  .womanImg {
    position: absolute;
    width: 21vw;
    transform: translateY(-3.4vw) translateX(62vw) scale(1.4);
    transition: transform 2s;
  }

  .green:hover > .womanImg {
    transform: translateY(1.64vw) translateX(66vw) scale(1.1);
  }

  .greenTitle {
    font-size: 6vw;
    font-weight: 900;
    margin-left: 2vw;
    margin-top: 0vw;
    color: #1e1c1c1e;
  }

  .PocketIcon {
    display: none;
  }

  .greenSubtitle {
    margin-left: 2vw;
    font-weight: 600;
    font-size: 4vw;
    color: #1e1c1c1e;
    transition: color 2s;
  }

  .green:hover > .greenTitle {
    color: #1e1c1c;
  }

  /* //    width: 87vw; */
  .yellow {
    margin-top: 4vw;
    margin-right: 2vw;
    height: 35vw;
    border-radius: 1vw;
    width: 46vw;
    background-color: #ffca43;
    margin-bottom: 1vw;
  }

  .aqua {
    margin-left: 2vw;
    margin-top: 4vw;
    border-radius: 1vw;
    width: 39vw;
    height: 35vw;
    background-color: #01af8b;
    margin-left: 1vw;
    cursor: pointer;
  }

  .greenShirtImg {
    position: absolute;
    width: 6vw;
    transform: translateX(35vw) translateY(2vw) rotate(40deg) scale(2.5);
  }

  .yellow:hover > .greenShirtImg {
    transform: translateX(35vw) translateY(-1vw) rotate(40deg) scale(2.5);
  }

  .blueShirtImg {
    width: 8vw;
    transform: rotate(-15deg) translateX(2vw) translateY(5vw) scale(2.5);
    transition: transform 2s;
  }

  .yellow:hover > .blueShirtImg {
    transform: rotate(-15deg) translateX(2vw) translateY(4vw) scale(2.5);
  }

  .yellowBold {
    font-size: 7vw;
    margin-top: 8vw;
  }

  .yellowLight {
    font-size: 6vw;
  }

  .grassesImg {
    width: 35vw;
    transform: translateY(-7vw) translateX(0vw) scale(1);
    transition: transform 2s;
  }

  .aqua:hover > .grassesImg {
    transform: translateY(-1vw) translateX(13vw) scale(0.7);
  }

  .aquaTitle {
    font-size: 7vw;
  }
  .aquasSubtitle {
    font-size: 6.5vw;
  }
  .blue {
    margin-top: 4vw;
    width: 87vw;
    height: 35vw;
  }

  .buttonBlue {
    width: 32vw;
    height: 8vw;
    margin-top: 2vw;
    font-size: 4vw;
  }

  .blueTitle {
    font-size: 5vw;
  }

  .shopIcon {
    width: 19vw;
  }
  .tennisImg {
    width: 15vw;
    position: absolute;
    transform: translateY(-8vw) translateX(62vw) rotate(-20deg) scale(2);
    transition: transform 2s;
  }

  .blue:hover > .tennisImg {
    transform: translateY(-4vw) translateX(62vw) rotate(0deg) scale(3);
    transition: transform 2s;
  }

  .glass {
    margin-top: 4vw;
    width: 87vw;
    height: 35vw;
  }

  .glass:hover > .glassesImg {
    transform: translateY(5vw) translateX(69vw) rotate(0deg) scale(2);
  }

  .glassesImg {
    width: 15vw;
    margin-top: 5vw;
    transform: translateY(5vw) translateX(69vw) rotate(0deg) scale(1.5);
    transition: transform 2s;
  }

  .glassTitle {
    font-size: 12vw;
  }

  .glassSubtitle {
    position: absolute;
    transform: translateY(-15vw);
    margin-left: 2vw;
    font-size: 10vw;
  }
}
