.navbar-container {
  margin-top: 1vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid 0.1vw #fff;
}

.logo {
  margin-left: 4vw;
  display: flex;
  align-items: center;
  font-size: 2vw;
  margin-bottom: 1vw;
}
.logo img {
  width: 2vw;
}
.logoBold {
  font-weight: 600;
}
/* //////////////////////////////// */

.navbar-items {
  font-size: 1.5vw;
  margin-right: 10vw;
  display: flex;
  width: 20vw;
  justify-content: space-between;
  margin-bottom: 1vw;
}

.navbar-search {
  margin-right: 4vw;
}
